html {
  width: 100%;
  height: 100%;
  touch-action: none;
  /* scroll-behavior: smooth; */
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  background: 
  linear-gradient(135deg, black 25%, transparent 25%) -50px 0,
  linear-gradient(225deg, black 25%, transparent 25%) -50px 0,
  linear-gradient(315deg, black 25%, transparent 25%),
  linear-gradient(45deg, black 25%, transparent 25%);	
  background-size: 2em 2em;
  background-color: #232323;
}

h1 {
  font-family: fantasy;
}

p {
  font-size: 1.5em;
}

.top-bar {
  width: 100%;
  background-color: rgba(64, 64, 64, 0.5);
  z-index: 2;
}

.top-bar > a {
  color: white;
  text-decoration: none;
  font-size: 2rem;
  font-family: fantasy;
}

#title-bar > a {
  position: absolute;
  top: 1.25rem;
  left: 2rem;
  z-index: 3;
  font-style: italic;
}

#title-bar > a:hover {
  background-color: initial;
  /* text-shadow: 1px 1px 3px white; */
}

#nav-bar {
  /* position: -webkit-sticky;   
  position: sticky; */
  position: relative;
  display: block;
  top: 0;
  line-height: 100%;
  text-align: center;
}

#nav-bar > a {
  display: inline-flex;
  flex-direction: column;
  padding: 2rem;
  padding-right: 2rem;
  padding-left: 2rem;
  padding-bottom: 2rem;
  margin: 0;
  border-right:  2px solid rgba(32, 32, 32, 0.9);
}

#nav-bar > a.no-border {
  border: none;
}

#nav-bar > a.selected {
  background-color: rgba(64, 64, 64, 0.75);
}

#nav-bar > a.selected::before {
  content: ".";
  position: absolute;
  display: block;
  top: 1rem;
  height: 100%;
  transform: scale(2);
}

#nav-bar > a:hover {
  background-color: rgba(64, 64, 64, 0.75);
}

@media screen and (max-aspect-ratio: 3/4) {
  #nav-bar {
      padding-top: 3rem;
  }

  #nav-bar > a {
    margin-top: 1rem;
  }  
}

.canvas-container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.site-body {
  position: absolute;
  display: none;
  /* width: 100%; */
  height: auto;
  min-height: 100%;
  z-index: 1;
  background-color: rgba(255, 255, 255, 1);
  padding-top: 3em;
  padding-left: 10em;
  padding-right: 10em;
}

.site-body.fade{
  display: block;
  opacity: 0;
  transition:opacity .3s linear;
  -moz-transition:opacity .3s linear;
  -webkit-transition:opacity .3s linear;
}

.site-body.active{
  display: block;
  opacity: 1;
  transition:opacity .3s linear;
  -moz-transition:opacity .3s linear;
  -webkit-transition:opacity .3s linear;
}

